.cv-header {
	background-color: #393939;
	color: white;
	padding: 0 20px;
	grid-column: 1 / 3;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.cv-header h1 {
	font-size: 3rem;
	margin-bottom: 10px;
}

.cv-header p {
	font-size: 1.4rem;
}

.cv-header span {
	display: flex;
	align-items: center;
	gap: 20px;
}
